.transportControl h1 {
    font-size: 36px;
    margin-top: 13px;
    margin-left: 55px;
    font-weight: 500;   
}

.transportControl .employeeLists {
    margin-top: 43px;
    margin-left: 42px;
    margin-right: 110px;
}
img.editPanel {
    cursor: pointer;
}
.employeeLists {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.employeeLists hr {
    border: 0.5px solid #C4C4C4;
}

.employeesList .addMenu {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-left: 13px;
    margin-bottom: 59px;
}

.employeesList .addMenu img {
    margin-left: 16px;
}

.employeesList table {
    font-size: 18px;
    font-weight: 300;
}

.employeesList table td {
    padding: 7px 7px;
    text-align: center;
    vertical-align: middle;
}

.employeesList table td.employeeName {
    text-align: left;
} 

.employeesList table td.active span{
    background-color: #E8E4F9;
    border-radius: 5px;
    padding: 5px 12px;
}

.employeesList table td:first-child {
    padding-left: 16px;
}

.employeesList table td:last-child {
    padding-right: 16px;
}

.employeesList table tr:hover {
    box-shadow: 1px 0px 15px #EBE4FF;
    cursor: default;
}

.employeesList .addMenu .addEmployee {
    cursor: pointer;
}
.modalForm {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #fff;
    box-shadow: 1px 0px 5px rgba(64, 59, 79, 0.5);
    padding: 6px 12px;
}

.modalForm .buttonsPanel {
    border: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.modalForm .buttonsPanel input {
    color: #fff;
    padding: 6px 12px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 300;
    text-align: center;
    border: 1px solid transparent;
    border-radius: 4px;
}

.modalForm .inputGroup input[type="text"] {
    text-align: center;
    width: 240px;
    font-size: 14px;
    font-weight: 300;
    color: #697079;
    padding: 6px 12px;
}

.modalForm .buttonsPanel #save {
    background-color: #3b9879;
    border-color: #3b9879;    
}

.modalForm .buttonsPanel #cancel {
    background-color: #d9534f;
    border-color: #d43f3a;
}

.modalForm h2 {
    font-size: 20px;
    font-weight: 300;
    margin-bottom: 18px;
}

.modalForm .inputGroup {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.modalForm .label {
    border: 0px !important;
    padding: 3px;
    font-size: 14px;
    font-weight: 300;
}
